html {
  font-size: 100%;
  box-sizing: border-box;
}

*, *::before, *::after {
  box-sizing: inherit;
}

body {
  margin: 0;
  padding: 0;
  font-family: var(--font-inter);
}

:root {
  --v-dark-blue-top-bg: hsl(232, 19%, 15%);
  --v-pale-blue-bg: hsl(225, 100%, 98%);
  --desat-dark-blue-bg: hsl(228, 28%, 20%);
  --l-grayish-blue-bg: hsl(227, 47%, 96%);
  --v-dark-blue-bg: hsl(230, 17%, 14%);
  --white-bg: hsl(0, 0%, 100%);
  --d-grayish-blue-text: hsl(228, 12%, 44%);
  --v-dark-blue-text: hsl(230, 17%, 14%);
  --desat-blue-text: hsl(228, 34%, 66%);
  --white-text: hsl(0, 0%, 100%);
  --instagram-start: hsl(37, 97%, 70%);
  --instagram-end: hsl(329, 70%, 58%);
  --toggle-start: hsl(210, 78%, 56%);
  --toggle-end: hsl(146, 68%, 55%);
  --limegreen: hsl(163, 72%, 41%);
  --brightred: hsl(356, 69%, 56%);
  --facebook: hsl(208, 92%, 53%);
  --youtube: hsl(348, 97%, 39%);
  --twiter: hsl(203, 89%, 53%);
  --toggle: hsl(230, 22%, 74%);
}

:root {
  --font-inter: "Inter", sans-serif;
}

h1, h2, h3 {
  margin-top: 0;
}

a, a:visited, a:active {
  text-decoration: none;
}

.card {
  padding: 1.25rem;
}

.toggle__button {
  width: 1rem;
  height: 1rem;
  background-color: var(--toggle);
  border-radius: 50%;
}